        <v-icon x-small @click="openMassEditDialog('input_max'), $event.stopPropagation()" color="primary">mdi-pencil</v-icon>&nbsp;
      </template>
      <template v-slot:header.scale_min="{ item }" >
        {{ toUpperTextKey('scale_min') }}&nbsp;
        <v-icon x-small @click="openMassEditDialog('scale_min'), $event.stopPropagation()" color="primary">mdi-pencil</v-icon>&nbsp;
      </template>
      <template v-slot:header.scale_max="{ item }" >
        {{ toUpperTextKey('scale_max') }}&nbsp;
        <v-icon x-small @click="openMassEditDialog('scale_max'), $event.stopPropagation()" color="primary">mdi-pencil</v-icon>&nbsp;
      </template>

      <template v-slot:item.actions="{ item }">
